export default {
	data() {
		return {
			fmList:[]
		};
	},
	onLoad() {
		this.getFm()
	},
	onPullDownRefresh(){
		console.log('下拉刷新')
		this.getFm()
	},
	methods:{
		getFmId(item){
			console.log(item.id)
			uni.navigateTo({
				url:'../music/music?id='+item.id
			})
			let items = {
				...item,
				al:{
					picUrl:item.album.picUrl,
					name:item.artists[0].name
				}
			}
			let playlist = {
				tracks:[items]
			}
			uni.setStorageSync('playlist',playlist)
		},
		getFm(){
			this.$request({
				url:"/personal_fm"
			}).then((data)=>{
				if(data.data.code == 200){
					this.fmList = data.data.data
					console.log(this.fmList)
					uni.stopPullDownRefresh()
				}
			})
		},
	}
}
